.Content {
    min-height: 700px;
    margin-top: 65px;
    position: relative;
}
.Navbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 50px;
    display: flex;
    background-color: #fff;
    z-index: 40;
    box-shadow: inset 0 -1px 1px rgb(204, 204, 204, .7);
}
.ErrMessage {
    background-color: #fff;
    padding: 20px 40px;
    position: absolute;
    left: 50%;
    top: 20px;
    transform: translateX(-50%);
    z-index: 30;
    border: 1px solid rgb(204, 204, 204, .7);
    box-shadow: inset 0 -1px 1px rgb(204, 204, 204, .7);
}
@media (min-width: 800px) {
    .Content {
        margin-left: 200px;
    }
}